export default {
  props: {
    pagination: Object,
  },
  computed: {
    backButton() {
      return this.pagination.page - 1 == 1 ||this.pagination.page - 1 == 0  ? '/' : `/p/${parseInt(this.pagination.page) - 1}`;
    },
    fwdButton() {
      return `/p/${parseInt(this.pagination.page) + 1}`;
    },
  },
};
